@import "global/variables.scss";


.week-product__wrapper {
  position: relative;
  width: 320px;
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px;
  box-sizing: border-box;

  @media (min-width: $width-tablet) {
    width: $width-tablet;
    padding-right: 50px;
    padding-left: 50px;
  }

  @media (min-width: $width-desktop) {
    width: $width-desktop;
    padding-top: 20px;
    padding-right: 75px;
    padding-left: 75px;
  }
}


.week-product__title {
  grid-area: title;
  padding-top: 31px;
  padding-bottom: 4px;

  @media (min-width: $width-tablet) {
    padding-top: 38px;
    padding-bottom: 50px;
  }

  @media (min-width: $width-desktop) {
    padding-top: 95px;
    padding-bottom: 22px;
    z-index: 1;
  }
}


.week-product__subtitle {
  grid-area: subtitle;
  align-self: center;
  justify-self: flex-end;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  color: $color-text-brand;
  background-color: $color-bg-primary;
  text-transform: uppercase;

  @media (max-width: $width-mobile-only) {
    margin-bottom: 18px;
  }

  @media (min-width: $width-tablet) {
    max-width: 100px;
    margin-right: 11px;
    margin-bottom: 15px;
    line-height: 17px;
    text-align: center;
  }

  @media (min-width: $width-desktop) {
    position: absolute;
    top: 123px;
    left: 92px;
    max-width: 130px;
  }
}


.week-product__article {
  @media (min-width: $width-tablet) {
    display: grid;
    grid-template-columns: 292px 140px 234px;
    grid-template-rows:
      min-content
      124px
      171px
      57px
      104px;
    grid-template-areas:
      "title title subtitle"
      "text image image"
      "table image image"
      "price image image"
      "button image image";
    padding-left: 0;
  }

  @media (min-width: $width-desktop) {
    grid-template-columns: auto 203px 380px;
    grid-template-rows:
      min-content
      162px
      auto
      57px
      125px;
    grid-template-areas:
      "title title title"
      "text image image"
      "table image image"
      "price image image"
      "button image image";
    padding-left: 175px;
  }
}


.week-product__text-content {
  grid-area: text;
  margin: 0;
  padding-bottom: 26px;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: $color-text-secondary;
  background-color: $color-bg-primary;

  @media (min-width: $width-tablet) {
    margin-top: -12px;
    font-size: 17px;
    line-height: 30px;
  }

  @media (min-width: $width-desktop) {
    margin-top: 14px;
    padding-bottom: 0;
  }
}


.week-product__table {
  min-width: 260px;
  grid-area: table;
  font-size: 15px;
  line-height: 20px;
  color: $color-text-secondary;
  background-color: $color-bg-primary;

  @media (min-width: $width-tablet) {
    padding-bottom: 57px;
  }

  @media (min-width: $width-desktop) {
    padding-bottom: 110px;
  }
}


.week-product__data {
  min-width: 67px;
  padding-top: 9px;
  padding-bottom: 9px;
  font-weight: 400;
  text-align: left;
  border-bottom: 1px solid $color-border-navigation;

  @media (min-width: $width-tablet) {
    min-width: auto;
    padding-top: 7px;
    padding-bottom: 10px;
  }
}


.week-product__row:first-child .week-product__data {
  padding-top: 0;
}


.week-product__image {
  grid-area: image;
  padding-top: 26px;
  padding-bottom: 19px;

  @media (min-width: $width-tablet) {
    min-width: 334px;
    min-height: 404px;
    padding-top: 0;
    padding-left: 42px;
  }

  @media (min-width: $width-desktop) {
    grid-area: image;
    min-width: 525px;
    min-height: 636px;
    margin-top: -140px;
    padding-left: 58px;
  }
}


.week-product__price {
  display: block;
  grid-area: price;
  padding-bottom: 24px;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: $color-text-secondary;
  background-color: $color-bg-primary;
  text-align: center;

  @media (min-width: $width-tablet) {
    font-size: 20px;
    padding-bottom: 33px;
  }
}


.week-product__button {
  grid-area: button;
  margin-bottom: 36px;
  font-size: 18px;
  line-height: 20px;
  text-align: center;

  @media (min-width: $width-tablet) {
    margin-bottom: 52px;
    font-size: 17px;
  }

  @media (min-width: $width-desktop) {
    margin-bottom: 73px;
  }
}
